.sobre p {
  color: #ffffff;
  font-size: 30px;
  font-weight: 300;
  text-align: center;
  letter-spacing: 1.5px;
  line-height: 1.5em;
  width: 100%;
  margin-bottom: 10px;
}
.sobre button {
  font-weight: 700;
  cursor: pointer;
  color: #ffff;
  border: 1px solid #ffffff;
  padding: 29px 100px;
  font-size: 18px;
  position: relative;
  background-color: transparent;
  transition: 0.4s ease-in-out;
