.contact {
  background: linear-gradient(90.21deg, #AA367C -5.91%, #4A2FBD 111.58%);
  padding: 60px 0 200px 0;
}

.contact img {
  width: 92%;
  padding-top: 150px;
}
.contact h2 {
  font-size: 45px;
  font-weight: 700;
  padding-bottom: 30px;
}
.contact form input, .contact form textarea {
  width: 100%;
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.5);
  border-radius: 20px;
  color: #fff;
  margin: 20px 0 8px 0;
  font-weight: 500;
  font-size: 18px;
  letter-spacing: 1px;
  transition: 0.3s ease-in-out;
  padding-top: 20px;
  padding: 29px 20px;
  }
.contact form input:focus, .contact form textarea:focus {
  background: #0000;

  }
  .contact form input::placeholder, .contact form textarea::placeholder {
    font-size: 20px;
    font-weight: 400;
    color: #fff;
  }
  .contact form input:focus::placeholder, .contact form textarea:focus::placeholder {
    color: #fff;
    opacity: 0.8;
    border-radius: 50px;
  }
  .contact form button {
    font-weight: 700;
    cursor: pointer;
    color: #ffff;
    border: 1px solid #ffffff;
    padding: 29px 90px;
    font-size: 18px;
    position: relative;
    background-color: transparent;
    transition: 0.2s ease-in-out;
    border-radius: 20px;
  }
  .contact form button span {
    z-index: 1;
    position: relative;
    filter: blur(0.2px);
  }
  .contact form button:hover {
    color: #fff;
    opacity: 0.8;
    border-radius: 20px;
  }
  .contact form button::before {
    border-radius: 20px;
    background-color: rgba(255, 255, 255, 0.1);
    content: "";
    width: 0;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 0;
    transition: 0.80s ease-in-out;
  }
  .contact form button:hover::before {
    width: 100%;
    
  }
  .modal-overlay{
    background-color: #4A2FBD;
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    opacity: 0.9;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .modal-content{
    width: 90%;
    height: 280px;
    max-width: 600px;
    background-color: white;
    border-radius: 5px;
    color: black;
    padding-bottom: 30px;
  }

  .modal-content h2{
    color: black;
    padding-top: 20px;
    font-size: 35px;
    justify-content: center;
    text-align: center;
  }
  
  .modal-content p{
    padding-top: 10px;
    padding-left: 30px;
    font-size: 20px;
    justify-content: center;
    color: black;
    margin-right: 20px;
  }

  .modal-content button{
    width: 50px;
    height: 50px;
    cursor: pointer;
    color: black;
    border: 1px solid black;
    padding: 15px;
    background-color: white;
    transition: 0.9s ease-in-out;
    border-radius: 30px;
    margin-left:540px;
    margin-top:20px;
  }

